import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { AddBinTransferComponent } from './add-bin-transfer.component';

const routes: Routes = [
  {
    path: '',
    component: AddBinTransferComponent
  }
]

@NgModule({
  declarations: [AddBinTransferComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ]
})
export class AddBinTransferModule { }
