import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductStockComponent } from './product-stock/product-stock.component';



const routes: Routes = [
  {
    path: 'search-product',
    component: ProductStockComponent
  },
  {
    path: 'add-bin-transfer',
    loadChildren: () => import('./add-bin-transfer/add-bin-transfer.module').then(m => m.AddBinTransferModule)
  },
  {
    path: 'transfer-order',
    loadChildren: () => import('./transfer-order/transfer-order.module').then(m => m.TransferOrderModule)
  }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OperationRoutingModule { }
